  /* active state */
  /* active state */
}
.o-input__input,
.o-input__textarea {
  background-color: transparent;
  font-size: 1.8rem;
  color: #05070a;
  padding: 1rem 1rem 1rem .5rem;
  display: block;
  width: 100%;
  border: none;
  border-bottom: 0.1rem solid rgba(5, 7, 10, 0.15);
}
.o-input__input:focus,
.o-input__textarea:focus {
  outline: none;
  color: #05070a;
}
.o-input__textarea {
  margin-top: 1rem;
  padding-top: 0;
  resize: none;
  height: 10rem;
}
.o-input__label {
  color: rgba(5, 7, 10, 0.25);
  font-size: 1.8rem;
  pointer-events: none;
  position: absolute;
  left: 5px;
  top: 3.5rem;
  transition: 0.2s ease all;
}
.o-input__input:focus ~ .o-input__label,
.o-input__input:not(.is-empty) ~ .o-input__label,
.o-input__textarea:focus ~ .o-input__label,
.o-input__textarea:not(.is-empty) ~ .o-input__label {
  top: 1.5rem;
  font-size: 1.4rem;
}
.o-input__input:focus ~ .o-input__label,
.o-input__textarea:focus ~ .o-input__label {
  color: #3773d7;
}
.o-input__bar {
  position: relative;
  display: block;
  width: 100%;
}
.o-input__bar:before,
.o-input__bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 0;
  position: absolute;
  background: #3773d7;
  transition: 0.2s ease all;
}
.o-input__bar:before {
  left: 50%;
}
.o-input__bar:after {
  right: 50%;
}
