/* ------------
 * Navigation
 */
.c-navigation {
  overflow: hidden;
  position: relative;
  display: block;
  background-color: #323c46;
  flex: 0 0 27rem;
}
.c-navigation::before {
  height: 100vh;
  background-color: rgba(5, 7, 10, 0.15);
  z-index: 99;
}
.c-navigation::before,
.c-navigation__header::before {
  display: block;
  content: "";
  width: .4rem;
  position: absolute;
  right: 0;
  top: 0;
  pointer-events: none;
}
/* ------------
 * Navigation Header
 */
.c-navigation__header {
  display: flex;
  position: relative;
  z-index: 100;
  width: 100%;
  height: 6rem;
  background-color: #ffd200;
  box-shadow: 0 0 2px rgba(5, 7, 10, 0.15);
}
.c-navigation__header::before {
  height: 100%;
  background-color: rgba(5, 7, 10, 0.05);
  z-index: 101;
}
.c-navigation__icon {
  cursor: pointer;
  flex: 0 0 6rem;
  height: 100%;
}
.c-navigation__icon--right {
  position: absolute;
  right: 0;
  top: 0;
  background-image: linear-gradient(90deg, rgba(255, 210, 0, 0) 0, #ffd200 20%, #ffd200 100%);
}
.c-navigation__icon--light .o-icon {
  opacity: .25;
  transition: opacity .3s ease;
}
.c-navigation__icon--light:hover .o-icon {
  opacity: 1;
}
.c-navigation__icon .o-icon {
  margin: 1.5rem;
  height: 3rem;
  width: 3rem;
  z-index: 10;
  position: relative;
}
.c-navigation__icon .o-link {
  position: relative;
  background-color: #f0c500;
}
.c-navigation__icon .o-link::before {
  content: "";
  display: block;
  z-index: 5;
  width: 0;
  height: 100%;
  position: absolute;
  background-color: #e6bd00;
  right: 0;
  top: 0;
  transition: width 0.3s ease;
}
.c-navigation__icon .o-link:hover::before {
  width: 100%;
  transition: width 0.5s ease;
}
.c-navigation__title {
  height: 2.2rem;
  flex: 1;
  font-size: 1.8rem;
  line-height: 2.2rem;
  padding: 0 1.5rem 0 1rem;
  margin: 1.9rem 0 2.3rem 0;
  overflow: hidden;
}
/* ------------
 * Navigation List
 */
.c-navigation__list {
  list-style: none;
  height: calc(94vh);
  overflow-y: scroll;
  padding-bottom: 6rem;
}
.c-navigation__item {
  position: relative;
  display: block;
  width: 100%;
  font-size: 1.6rem;
  height: 5rem;
  cursor: pointer;
  background-color: #323c46;
  transition: background-color 0.3s ease;
}
.c-navigation__item:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #3773d7;
  z-index: 0;
}
.c-navigation__item:hover {
  transition: background-color 0.3s ease;
  background-color: rgba(55, 115, 215, 0.75);
}
.c-navigation__item.is-active {
  color: #ffffff;
}
.c-navigation__item.is-active:after {
  transition: width 0.3s ease;
  width: 100%;
}
.c-navigation__link {
  height: 100%;
  width: 100%;
  padding: 1.5rem 1rem 1.9rem 1.5rem;
  position: relative;
  z-index: 10;
  color: rgba(255, 255, 255, 0.65);
}
.c-navigation__link.c-navigation__link--with-icon {
  padding-left: 4.5rem;
}
.c-navigation__link .o-icon {
  position: absolute;
  height: 2rem;
  top: 1.5rem;
  left: 1.5rem;
  fill: rgba(255, 255, 255, 0.25);
  transition: fill 0.3s ease;
}
.is-active .c-navigation__link {
  color: rgba(255, 255, 255, 0.85);
  transition: color 0.3s ease;
}
.is-active .c-navigation__link .o-icon {
  fill: rgba(255, 255, 255, 0.65);
}
.is-active .c-navigation__link:hover .o-icon {
  fill: #ffffff;
}
.c-navigation__delete {
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  height: 5rem;
  width: 5rem;
  padding: 1.5rem;
  background-color: rgba(5, 7, 10, 0.05);
  transition: opacity .3s ease;
  z-index: 20;
}
.c-navigation__delete:hover {
  transition: color 0.3s ease, background-color 0.3s ease;
  background-color: rgba(5, 7, 10, 0.15);
}
.c-navigation__item:hover .c-navigation__delete,
.c-navigation__item.is-active .c-navigation__delete {
  opacity: 1;
}

